body {
  background-color: black;
}

img {
  display: block;
}

.search-form {
  display: flex;
  justify-content: center;
}

input {
  width: 200px;
  height: 30px;
  border-radius: 8px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  background-color: #ffffff;
  margin: 10px;
}

.photo-card img {
  max-height: 350px;
  min-height: 350px;
  max-width: 500px;
  min-width: 500px;
  overflow: hidden;
}

.stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 80px;
}

#load-btn {
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  text-align: center;
  border-radius: 10px;
}

.hidden {
  visibility: hidden;
}
